@charset "utf-8";
/* cyrillic Regular*/
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: local("Oswald Regular"), local("Oswald-Regular"), url("../fonts/oswaldregular.woff2") format("woff2"),
  local("Oswald Regular"), local("Oswald-Regular"), url("../fonts/oswaldregular.woff") format("woff");
  unicode-range: u+0400-045f, u+0490-0491, u+04b0-04b1, u+2116;
}

/* cyrillic Medium*/
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  src: local("Oswald Medium"), local("Oswald-Medium"), url("../fonts/oswaldmedium.woff2") format("woff2"),
  local("Oswald Medium"), local("Oswald-Medium"), url("../fonts/oswaldmedium.woff") format("woff");
  unicode-range: u+0400-045f, u+0490-0491, u+04b0-04b1, u+2116;
}

/* latin Regular*/
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: local("Oswald Regular"), local("Oswald-Regular"), url("../fonts/oswaldregular.woff2") format("woff2"),
  local("Oswald Regular"), local("Oswald-Regular"), url("../fonts/oswaldregular.woff") format("woff");
  unicode-range: u+0000-00ff, u+0131, u+0152-0153, u+02bb-02bc, u+02c6, u+02da, u+02dc, u+2000-206f, u+2074, u+20ac, u+2122, u+2191, u+2193, u+2212, u+2215, u+feff, u+fffd;
}

/* latin Medium*/
@font-face {
  font-family: "Oswald";
